<template>
    <div class="early-warning-router-container">
        <div class="withinBox">
            <!-- <p class="title">数据分析</p>
            <p class="qualityStyle">空气质量分析</p> -->
            <div class="l_box">
                <div class="l_headerStl">
                    <p class="pm">目标设置:</p>

                    <el-select
                        disabled
                        class=" my-select"
                        style="width: 80px"
                        v-model="changeValue"
                        @change="valueChange"
                    >
                        <el-option
                            v-for="item in changeValueOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        >
                        </el-option>
                    </el-select>
                    <el-input
                        class="my-input"
                        style="width: 80px"
                        v-model="inputValue"
                        placeholder="请输入内容"
                    ></el-input>
                    <p class="pm" style="margin-left:-40px;">
                        <span v-if="changeValue == 'PM25'">ug/m3</span>
                        <span v-else-if="changeValue == '降尘量'">t/Km3</span>
                        <span v-else>天</span>
                    </p>
                    <div class="l_date">数据时间: {{ nowTime }}</div>
                </div>

                <div class="l_table">
                    <div class="table_headerStl">
                        <span class="w_413">控制类型</span>
                        <span class="w_413">年累计</span>
                        <span class="w_413">同比</span>
                        <span class="w_413">剩余控制量</span>
                    </div>
                    <div class="table_body">
                        <div
                            class="table_index"
                            :class="{
                                pm_bg:
                                    item.type == 'PM25' ||
                                    item.type == '优良天' ||
                                    item.type == '重污染天',
                            }"
                            v-for="(item, i) in tableData"
                            :key="i"
                        >
                            <div class="t_i">{{ item.type }}</div>
                            <div class="t_i">{{ item.year }}</div>
                            <div class="t_i">{{ item.contrast }}
                                <span v-if="item.type == 'PM25' || item.type == '降尘量'">%</span>
                                <span v-else>天</span>
                            </div>
                            
                            <div class="t_i">
                                <span>{{item.defaultValue- item.year }}</span>
                            </div>
                            <!-- <div class="t_i">{{ item.defaultValue }}</div> -->
                        </div>
                    </div>
                </div>
            </div>
            <div class="r_box">
                <!-- <Echart class="searchStl" :newOption="newOption"></Echart> -->
            </div>
        </div>
    </div>
</template>

<script>
import { http } from "@/utils/request";
import Echart from "./Echarts.vue";
export default {
    components: {
        Echart,
    },
    data() {
        return {
            nowTime: "",
            newOption: [
                // {
                //     value: [2, 3, 2, 2],
                //     name: '北京市',
                // },
                // {
                //     value: [4, 4, 4, 5],
                //     name: '大兴区'
                // }
            ],
            crt_bg: "3",
            inputValue: 37,
            changeValue: "PM25",
            changeValueOptions: [
                {
                    value: "O3",
                    label: "O3",
                },
                {
                    value: "PM25",
                    label: "PM25",
                },
                {
                    value: "优良天",
                    label: "优良天",
                },
                {
                    value: "重污染天",
                    label: "重污染天",
                },
                {
                    value: "降尘量",
                    label: "降尘量",
                },
            ],
            //table_header: ['目标设置:', 'PM2.5', '35', 'ug/m3'],
            tableData: [
                // {
                //     type: 'PM2.5',
                //     year: '',
                //     contrast: '',
                //     remaining: '',
                // },
                // {
                //     type: '降尘量',
                //     year: '',
                //     contrast: '',
                //     remaining: '',
                // },
                // {
                //     type: '优良天',
                //     year: '',
                //     contrast: '',
                //     remaining: '',
                // },
                // {
                //     type: '重污染天',
                //     year: '',
                //     contrast: '',
                //     remaining: '',
                // },
                // {
                //     type: 'O3超标天',
                //     year: '',
                //     contrast: '',
                //     remaining: '',
                // },
            ],
        };
    },
    created() {
        this.getTableDataList();
        this.getAirDataList();
    },
    methods: {
        valueChange(value) {
            if (value == "O3") {
                this.inputValue = 90;
            } else if (value == "PM25") {
                this.inputValue = 37;
            } else if (value == "优良天") {
                this.inputValue = 264;
            } else if (value == "重污染天") {
                this.inputValue = 11;
            } else if (value == "降尘量") {
                this.inputValue = 0;
            }
        },
        header_clk(i) {
            this.crt_bg = i + 1;
        },
        // 表格
        getTableDataList() {
            let json = { username: "api", password: "a123456b" };
            http.get(
                "https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/yeardatas",
                {
                    params: {
                        json,
                    },
                }
            ).then((res) => {
                if (res.data.code === 2000) {
                    this.nowTime = res.data.data_time.slice(0, 10);
                    let tableData = res.data.result;
                    // this.tableData = this.tableData
                    for (let ele in tableData) {
                        this.tableData.push({
                            type: ele,
                            year: tableData[ele][0].value,
                            contrast: ele=='降尘量'||ele=='PM25' ?  tableData[ele][0].yoy_ratio :tableData[ele][0].yoy_value,
                        });
                    }
                    this.tableData.forEach(item=>{
                        if(item.type=='O3'){
                            item.defaultValue= 90
                        }else if(item.type=="PM25"){
                            item.defaultValue= 37
                        }else if(item.type=="优良天"){
                            item.defaultValue= 264
                        }else if(item.type=="重污染天"){
                            item.defaultValue= 11
                        }else if(item.type=="降尘量"){
                            item.defaultValue= 0
                        }
                    })
                }
            });
        },
        // 空气质量分析图表 数据
        // stationid: 14 (⼤兴⻩村) 455 (⼤兴旧宫)
        getAirDataList() {
            let json = {
                username: "api",
                password: "a123456b",
                stationid: "14",
            };
            http.get(
                "https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/std/radar",
                {
                    params: {
                        json,
                    },
                }
            ).then((res) => {
                if (res.data.code === 2000) {
                    this.newOption = res.data.result;
                    this.newOption = this.newOption.map((item, index) => {
                        return {
                            value: item.list,
                            name: item.name,
                        };
                    });
                }
            });
        },
    },
};
</script>

