                        .t_i {
                            width: 413px;
                            height: 102px;
                            border-right: 2px solid #8ecafd;
                            float: left;
                            font-size: 21px;
                            font-weight: 600;
                            color: #e4f1ff;
                            text-align: center;
                            line-height: 102px;
                        }
                        .t_i:last-child {
                            border: none;
                        }
                    }
                }
            }
        }
    }
}
