<template>
    <div>
        <div ref="storeEcharts" :style="{ width: width, height: height }"></div>
    </div>
</template>

<script>
import * as echarts from "echarts";
export default {
    props: ["newOption"],
    data() {
        return {
            dataArr: [],
            width: "400px",
            height: "600px",
            myOption: {
                title: {
                    // text: 'Basic Radar Chart1'
                },
                // legend: {
                //     data: ["北京市", "大兴区"],
                // },

                radar: {
                    splitArea: {
                        areaStyle: {
                            color: ["#17EADF","#77EADF",  "#64AFE9", "#428BD4"],
                            shadowColor: "rgba(0, 0, 0, 0.2)",
                            shadowBlur: 10,
                        },
                    },
                    shape: "circle",
                    indicator: [
                        // { name: "PM2.5", max: 5 },
                        // { name: "NO2", max: 5 },
                        // { name: "CO", max: 5 },
                        // { name: "SO2", max: 5 },
                        { name: "特征值", max: 5 },
                        { name: "标准差", max: 5 },
                        { name: "上限值", max: 5 },
                        { name: "下限值", max: 5 },
                    ],
                },
                series: [
                    {
                        name: "Budget vs spending",
                        type: "radar",

                        data: [],
                    },
                ],
            },
        };
    },
    watch: {
        newOption(val) {
            console.log(val);
            this.dataArr = val;
            this.initChart();
        },
    },
    mounted() {
        console.log;
        this.dataArr = this.newOption;
        this.initChart();
    },
    methods: {
        initChart() {
            // 基于准备好的dom，初始化echarts实例
            this.chart = echarts.init(this.$refs.storeEcharts);
            this.chart.setOption(this.myOption);
            this.chart.setOption({
                series: [{ data: this.dataArr }],
            });
        },
    },
};
</script>